/* LOADING TRANSITION */
.site-loading {
    border: 8px solid #f3f3f3;
    border-top: 8px solid black; 
    border-radius: 50%;
    width: 75px;
    height: 75px;
    margin: 25px auto;
    animation: spin 0.9s linear infinite;
    z-index: -1;
    position: relative;
    text-align: center;
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}


@keyframes fadeIn {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

@keyframes fadeInUp {
    0% {
        opacity: 0;
        transform: translate3d(0, 100%, 0);
    }

    100% {
        opacity: 1;
        transform: none;
    }
}

@keyframes fadeInRight {
    0% {
        opacity: 0;
        transform: translate3d(100%, 0, 0);
    }

    100% {
        opacity: 1;
        transform: none;
    }
}