@import 'SiteCss/CSS/_mixins';

h1, 
h2, 
h3,
h4, 
h5, 
h6 {
    text-align: center;
    padding: 20px 0px;
    margin-top: 0;
    margin-bottom: 0.5rem;
}

.site-body {
    margin-top: 80px;
}

p {
    padding: 10px 45px;
    width: 100%;
}

hr {
    width: 80%;
    @include margin25XautoY;
}

@media screen and (max-width: 640px) {
    p {
        padding: 10px 25px;
    }
}